//btn back to main, socials
#rest-header {
	background-color: ac(primary);
}
#rest-header .top {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	font-family: $global-font-family;
	align-items: center;
}

.top-logo {
	width: 100%;
	height: 100%;
	& img {
		display: block;
	}
	@media (min-width: $b-medium) {
		max-width: 23.75rem;
	}
}

.top-address {
	display: none;
	flex-flow: row nowrap;
	justify-content: center;
	align-items: center;
	gap: 2rem;
	font-size: 2.25rem;
	& a {
		display: flex;
		flex-flow: row-reverse nowrap;
		justify-content: center;
		align-items: flex-end;
		&:hover {
			color: ac(secondary);
			& path {
				fill: ac(secondary);
			}
		}
	}
	& path {
		fill: ac(white);
	}
	@media (min-width: $b-large) {
		display: flex;
		width: 100%;
		margin-left: -15rem;
	}
	@media (min-width: $b-xlarge-h) {
		margin-left: 0;
		max-width: 28rem;
	}
}

.top-site-nav {
		display: flex;
		flex-flow: column nowrap;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 999;
		transition: all .3s ease-in-out;
		width: 6rem;
		height: 6rem;
		@media (min-width: $b-xlarge-h) {
			position: relative;
			flex-flow: row nowrap;
			align-content: center;
			width: 100%;
			height: 100%;
		}
		& .site-nav-list {
			display: none;
			width: 100%;
			margin: 0;
			padding: 0;
			list-style: none;
			background-color: ac(white);
			@media (min-width: $b-xlarge-h) {
				display: flex;
				background-color: transparent;
				justify-content: center;
			}
			& li {
				padding: 0;
				margin: 0;
				& a {
					display: block;
					padding: 2.5rem 2rem;
					width: 100%;
					font-size: 5rem;
					color: ac(black);
					font-family: $second-font-family;
					transition: all .3s ease-in-out;
					border: solid .1rem transparent;
					@media (min-width: $b-xlarge-h) {
						color: ac(white);
						font-size: 2.25rem;
						padding: 1rem 2rem;
					}
				}
				& a:hover {
					text-decoration: none;
					background-color: ac(secondary);
					color: ac(primary);
				}
			}
			& .current-menu-item {
				background-color: ac(secondary);
				@media (min-width: $b-xlarge-h) {
					border: solid .1rem ac(secondary);
					background-color: transparent;
					color: ac(white);
					font-size: 2.25rem;
			}
		}
	}
}

.site-nav-list-active {
	width: 100vw;
	height: 100vh;
	justify-content: center;
	background-color: ac(white);
	& .site-nav-list {
		display: block;
	}
	@media (min-width: $b-medium) {
		max-width: 50%;
	}
}

.top-site-nav .hum-btn {
	position: absolute;
	top: 2rem;
	right: 2rem;
	display: flex;
	flex-flow: column nowrap;
	width: 6rem;
	height: 6rem;
	justify-content: space-around;
	align-items: center;
	align-content: center;
	list-style: none;
	margin: 0;
	padding: 0;
	padding: 1rem;
	background-color: ac(secondary);
	& li {
		width: 80%;
		height: .5rem;
		background-color: ac(black);
	}
	@media (min-width: $b-xlarge-h) {
		display: none;
	}
}

.top-site-nav .active-hum-btn {
	& li {
		position: absolute;
		transition: all .3s ease-in-out;
		top: 45%;
	}
	& li:first-child {
		rotate: -45deg;
	}
	& li:nth-child(2) {
		display: none;
	}
	& li:last-child {
		rotate: 45deg;
	}
}

@media (min-width: $b-medium) {

}

