//logo burger address
#rest-header .bottom {
	display: flex;
	flex-flow: row nowrap;
	justify-content: space-between;
	@media (min-width: $b-xlarge) {
		margin-top: 8rem;
	}

	& a.logo-hor {
		order: 1;
		 & img {
			 max-width: 20.625rem;
			 height: 100%;
			 @media (min-width: $b-xlarge) {
				 max-width: 41rem;
			 }
			}
			@media (min-width: $b-xlarge) {
				order: 0;
			}
		}
		
	& address {
		display: none;
		@media (min-width: $b-xlarge) {
			display: block;
		}
		& ul {
			margin: 0;
			padding: 0;
			list-style: none;
			text-align: right;
			& li a {
				font-size: 2.375rem;
			}
			& li:nth-child(2) a {
				font-size: 2rem;
			}
		}
	}
}


